import React, {useEffect, useState} from 'react'
import styled from 'styled-components'
import { Close } from '@styled-icons/evaicons-solid';
import './MiniModal.css'

const Alert = styled('div')`
    border-radius:14px;
    background-color:black;
    color:white;
    padding:10px 18px;
    position: fixed;
    top:20px;
    z-index:3000;
    left:45vw;
`;

const Copy = styled('div')`
    border-radius:14px;
    background-color:white;
    color:black;
    padding:10px 18px;
    position: fixed;
    top:20px;
    z-index:3000;
    left:45vw;
    box-shadow:2px 4px 14px #e2e2e2;
`;

const Deploy = styled('div')`
    background: #FFFFFF;
    margin-top: 10px;
    box-shadow: 2px 4px 20px #E5E5E5;
    border-radius: 20px;
    color:black;
    padding:10px 18px;
    position: fixed;
    top:20px;
    z-index:3000;
    left:30vw;
    box-shadow:2px 4px 14px #e2e2e2;
    text-align: center;
`

function MiniModal({open, setOpen, copy, deploy, history, text}) {

    useEffect(() => {
        if(open && !deploy){
            setTimeout(() => {
                setOpen(false);
            }, 2100)
        }
    })

    if(open){
        if(copy){
            return (
                <Copy data-aos="zoom-in-down" data-aos-duration="9000" >
                      .
                </Copy>
            )
        }
        else if(deploy){
            return(
                <Deploy>
                    <div className="modal-big-title">
                          ! 
                    </div>
                    <div className="modal-big-title sub">
                           ''    '' , <br />
                          ''  . <br />
                             . <br />
                    </div>
                    <div className="check_button" onClick={() => {
                    setOpen(false);
                    history.go();
                    }}></div>
                </Deploy>
            )
        }
        else if(text){
            return (
                <Alert data-aos="zoom-in-down" data-aos-duration="9000" >
                    {text}
                </Alert>
            )
        } else{
            return (
                <Alert data-aos="zoom-in-down" data-aos-duration="9000" >
                     .
                </Alert>
            )
        }
    }
    else{
        return(<></>)
    }
}

export default MiniModal
